.select {
  position: relative;
}

.select.active .select__body {
  height: auto;
  max-height: 500px;
}

.select.active .select__icon {
  transform: scaleY(-1);
}
.select.active .select__body {
  display: block;
}
.select.active .select__header {
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
}

.select__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 130px;
  height: 26px;
  padding: 0 8px;
  border: 2px solid #474747;
  cursor: pointer;
  grid-gap: 8px;
}

.select__current {
  font-style: normal;
  font-weight: 500;
  font-size: 10px;
  line-height: 100%;
  font-family: var(--font-family);
  color: var(--color-a);
  text-transform: capitalize;
  letter-spacing: 0.05em;
}

.select__icon {
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 13px;
  height: 6px;
  text-align: center;
  transition: all 0.4s;
}

.select__body {
  position: absolute;
  right: 0;
  left: 0;
  z-index: 5;
  display: none;
  width: 100%;
  max-height: 0;
  margin-top: 10px;
  padding: 0;
  overflow: hidden;
  border-top: none;
  border-radius: 0 0 2px 2px;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
}

.select__item {
  width: 100%;
  margin: 0 auto;
  padding: 0 8px;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 100%;
  font-family: var(--font-family);
  color: var(--color-a);
  text-transform: capitalize;
  letter-spacing: 0.05em;
  cursor: pointer;
  opacity: 0.5;
  transition: all 0.4s;
  user-select: none;
}
.select__item:hover {
  opacity: 1;
}

.select__wrapp {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 12px 0 10px;
  background: var(--color-d);
  border: 1px solid var(--color-e);
  grid-gap: 8px;
}

.select__item:hover {
  color: #ffffff;
}

.main-title {
  font-style: normal;
  font-weight: 600;
  font-size: clamp(40px, 5vw, 60px);
  line-height: 125%;
  font-family: var(--font-family);
  text-align: center;
  color: var(--color-a);
  text-transform: uppercase;
}

.custom-icon {
  display: block;
  width: fit-content;
  margin: 0 auto;
  text-align: center;
  position: relative;
}
.custom-icon::before, .custom-icon::after {
  position: absolute;
  width: min(9.306vw, 134px);
  height: min(8.194vw, 118px);
  content: "";
  background-image: var(--title-icon);
  top: 50%;
  transform: translateY(-50%);
  z-index: 20;
  background-size: contain;
}
.custom-icon::before {
  transform: translateX(-100%) translateY(-50%);
  left: -10px;
}
.custom-icon::after {
  transform: scaleX(-1) translateX(-100%) translateY(-50%);
  right: -10px;
}

.oferta-info {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 17px;
  color: var(--color-e);
  margin-top: -17px;
}
.oferta-info a {
  color: var(--color-i);
  text-decoration: underline;
  display: inline;
}

.show-more {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 202px;
  height: 48px;
  font-style: normal;
  font-weight: 700;
  font-size: 15px;
  font-family: var(--font-family);
  color: var(--color-a);
  text-transform: uppercase;
  border: 2px solid var(--plus-bg);
  transition: all 0.4s;
}
.show-more:hover {
  background-color: var(--plus-bg);
  transform: scale(1.05);
}
.show-more.disable {
  opacity: 0.3;
  pointer-events: none;
}

.error-message {
  font-style: normal;
  font-weight: 400;
  font-size: clamp(13px, 1.25vw, 15px);
  line-height: 110%;
  font-family: var(--font-family);
  color: var(--plus-bg);
}

.red-border {
  padding: clamp(10px, 1.25vw, 15px) clamp(15px, 1.667vw, 20px);
  font-style: normal;
  font-weight: 700;
  font-size: 15px;
  line-height: 100%;
  font-family: var(--font-family);
  color: var(--color-d);
  text-transform: uppercase;
  border: 2px solid var(--plus-bg);
  transition: all 0.4s;
}
.red-border:hover {
  background-color: var(--plus-bg);
  color: var(--color-a);
}

