/*   input c type="checkbox" */
input[type=radio]:disabled + span:before,
input[type=checkbox]:disabled + span:before,
input[type=radio]:disabled + span,
input[type=checkbox]:disabled + span {
  opacity: 0.3;
  pointer-events: none;
}

.custom-checkbox {
  display: flex;
  align-items: center;
  position: relative;
}

.custom-checkbox > input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

/*   label,   .custom-checkbox */
.custom-checkbox > span {
  display: inline-flex;
  align-items: center;
  user-select: none;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 100%;
  color: var(--color-a);
}
@media screen and (max-width: 1025px) {
  .custom-checkbox > span {
    font-size: 14px;
  }
}

/*   label  before    */
.custom-checkbox > input[type=checkbox] + span::before {
  content: "";
  display: inline-block;
  width: 31px;
  height: 31px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 2px solid var(--plus-bg);
  border-radius: 4px;
  margin-right: 15px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 17px 17px;
}
@media screen and (max-width: 1025px) {
  .custom-checkbox > input[type=checkbox] + span::before:not(.choice-button) {
    width: 15px;
    height: 15px;
    margin-right: 7px;
    background-size: 70%;
  }
}

.custom-checkbox > input[type=radio] + span::before {
  content: "";
  display: inline-block;
  width: 31px;
  height: 31px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 2px solid var(--plus-bg);
  border-radius: 50%;
  margin-right: 15px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 17px 17px;
}
@media screen and (max-width: 1025px) {
  .custom-checkbox > input[type=radio] + span::before:not(.choice-button) {
    width: 15px;
    height: 15px;
    background-size: 70%;
    margin-right: 7px;
  }
}

/*   ,    checked */
.custom-checkbox > input:checked + span::before {
  background-image: url("data:image/svg+xml,%3Csvg width='17' height='17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='1' y='1' width='15' height='15' rx='7.5' fill='%23A5340F' stroke='%23A5340F' stroke-width='2'/%3E%3C/svg%3E");
}

/*   ,    disabled */
